





































	import server from '../../store/server.js';
	export default {
		onLoad() {
			this.checkLogin();
			this.Token = server.net.GetSessionToken();
			this.startGetPushMessage();
		},
		onUnload() {
			//程序退出后清空token 如果不自动登陆 下次需要登陆
			server.net.ClearSessionToken();
		},
		data(){
			return{
				Token:'',
				title: 'Hello',
				qualityTitle: '<h4 class="module-title">质量安全管理系统</h4>',
				sceneTitle: '<h4 class="module-title module-title-right">现场管理系统</h4>',
				scheduleTitle: '<h4 class="module-title">进度管理系统</h4>',
				fileTitle: '<h4 class="module-title module-title-right">文件管理系统</h4>'
			}
		},
		methods: {
			openViews(url) {
				uni.navigateTo({
					url: url
				})
			},
			checkLogin() {
				let localToken = server.net.GetLocalToken(); 
				let sessionToken = server.net.GetSessionToken(); 
				console.log("localToken：" + localToken + ",sessionToken:" + sessionToken);
				if (sessionToken == ''&& localToken == '') {
					uni.redirectTo({
						url: '../login/login' 
					})
					console.log("没有保存到本地Token")
				} else 
				{
					if(sessionToken == ''&& localToken != '') {
						server.net.SetSessionToken(localToken); 
					}
					this.getClientID();
				}
			},
			getClientID(){



























			},
			startGetPushMessage(){
				 // 监听点击消息事件
                plus.push.addEventListener( "click", function( msg ) {
					//plus.ui.alert(JSON.stringify(msg));
					uni.navigateTo({
						url: '../message/messageList'
					});
                }, false );
                // 监听在线消息事件
                plus.push.addEventListener( "receive", function( msg ) {
					if (msg.aps) {//ios应用在线会触发此事件,消息不进入通知栏
                        uni.navigateTo({
							url: '../message/messageList'
						});
					}
                }, false );
			}
				
		}
	}



